import { Config } from './Config';
export const NEW_WORKER = {
    l: 0,
    e: 0,
    s: 0,
};
export class Worker {
    constructor(job, data = NEW_WORKER) {
        this.job = job;
        this.level = data.l;
        this._experience = data.e;
        this.skills = data.s;
    }
    hasSkill(s) {
        return this.skills & s;
    }
    hasAllSkills(s) {
        return (this.skills & s) === s;
    }
    get experience() { return this._experience; }
    get expNeededForLevel() { return Config.Worker.LevelingExp[this.level]; }
    get canLevel() { return this._experience >= Config.Worker.LevelingExp[this.level]; }
    addExperience(exp) {
        this._experience += exp;
        // cap experience, to require leveling up before continuing to progress
        if (this._experience >= Config.Worker.LevelingExp[this.level]) {
            this._experience = Config.Worker.LevelingExp[this.level];
        }
    }
    resetExperience() {
        this._experience = 0;
    }
    levelUp(newSkill) {
        this._experience = 0;
        this.level++;
        this.skills |= newSkill;
    }
    toJSON() {
        return {
            l: this.level,
            e: this._experience,
            s: this.skills,
        };
    }
}
