export var Resources;
(function (Resources) {
    Resources["None"] = "n";
    Resources["Gold"] = "g";
    Resources["Herbs"] = "h";
    Resources["Potions"] = "p";
    Resources["Treasure"] = "t";
})(Resources || (Resources = {}));
const ALL_RESOURCES = [Resources.Gold, Resources.Herbs, Resources.Potions, Resources.Treasure];
export class ResourcePool {
    constructor() {
        this.pool = Object.create(null);
        for (const r of ALL_RESOURCES) {
            this.pool[r] = 0;
        }
    }
    clonePool() {
        return Object.assign({}, this.pool);
    }
    getResourceCount(r) {
        return this.pool[r] || 0;
    }
    addResource(r, count) {
        if (r === Resources.None)
            return;
        this.pool[r] += count;
    }
    removeResource(r, count) {
        if (r === Resources.None)
            return true;
        if (this.pool[r] >= count) {
            this.pool[r] -= count;
            return true;
        }
        return false;
    }
    load(data) {
        this.pool = Object.create(null);
        for (const r of ALL_RESOURCES) {
            this.pool[r] = data[r] || 0;
        }
    }
    toJSON() {
        const out = {};
        for (const r of ALL_RESOURCES) {
            out[r] = this.pool[r];
        }
        return out;
    }
}
